body{
    margin: 0;
    padding: 0%;
    box-sizing: border-box;
    font-family: 'rubik','inter';
    background-color: #0D0D2B;
    position: relative;
}
a{
    text-decoration: none;
    margin: .3rem;
}
span{
    padding: 2pt;
}
ul li{list-style-type: none;
}
.flex{display: flex;}
.all{max-width: 1220px;margin: 0 auto;}



/* header design start */

header{

    background-color: #0D0D2B;
    color: white;
    position: relative;
    height: 850px;
    overflow: visible;
}
.navbar{
    animation-name: fadeIn;
    animation-duration: 4s;
    animation-delay: 1s;
    max-width: 1200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    justify-items: center;
    margin: 60px auto;
}

.list{
    display:flex ;
 margin: 0 .8rem;
 padding: 0 .2rem; 
 justify-content: center;
 align-items: center;
}
.list2{
    display:flex ;
 margin: 0 .8rem;
 padding: 0 .2rem;
 justify-content: center;
 align-items: center;
}
.nav-spec{
    border-right:2px solid rgba(189, 182, 171,0.5);
padding-right: 5px;}
.nav-spec2{
     background-color: #3671E9;
     border: 0ch;
     border-radius: 32px;
     padding: 14px 32px;    
}
.hero{
    animation-name: fadeIn;
    animation-duration: 4s;
    animation-delay: 4s;
    display: flex;
    background-image: url("./Crypto-images/hero-bg.png");
    /* background-position-x: right;
    background-position-y: bottom; */
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    justify-content: center;
    align-items: center;
}.hero--span1{
    color: #fff;
    font-size: 16px;
    line-height: 28px;
    border-radius: 32px;
    margin: 4px;
    background-color:rgba(255, 255, 255, 0.1);
}
.hero--span{
    color: #0D0D2B;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    background-color: #fff;
    border-radius: 32px;
    margin: 4px;
}
.heroDiv h1{
    color: #fff;
    font-weight: 700;
    font-size: 64px;
    line-height: 76px;
    width: 588px;
}
.heroDiv p{
    color: #e0e0e0;
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    width: 435px;
}
.heroDiv button{
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    background-color: #3671E9;
    border-radius: 32px;
    border: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 16px 16px 16px 24px;
    gap: 24px;
}
.arrow-left{
    width: 32px;
    height: 32px; 
}
.none{
    position: absolute;
    width: 120px;
    height: 1000.26px;
    left: 191px;
    top: 1.86px;
    background: linear-gradient(180.09deg, rgba(255, 255, 255, 0) 0.49%, rgba(255, 255, 255, 0.04) 39.06%, rgba(255, 255, 255, 0) 99.92%);
    transform: rotate(-120deg);
}
